:root {
    --primary: #fff;
    --exclusive: #E9C46A;
    --brandColor: linear-gradient(90deg, rgb(109, 104, 117) 0%, rgb(181, 131, 100) 100%);
    --secondary: #323036;
}
.project {
    padding: 4rem;
    background: var(--brandColor)
}
  
h1 {
    text-align: center;
}
  
.project__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
}
  
.project__wrapper {
    position: relative;
    margin: 50px 0 45px;
}
  
.project__items {
    margin-bottom: 24px;
}

.project__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
}
  
.project__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(109, 104, 117,0.017);
    -webkit-filter: drop-shadow(0 6px 20px rgba(120, 108, 121, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}
  
.project__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
}
  
.fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
}
  
.project__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: var(--primary);
    background-color: var(--exclusive);
    box-sizing: border-box;
}
  
.project__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
}
  
.project__item__img:hover {
    transform: scale(1.1);
}
  
.project__item__info {
    padding: 20px 30px 30px;
}
  
.project__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}
  
@media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
}
  
@media only screen and (min-width: 1024px) {
    .project__items {
      display: flex;
    }
}
  
@media only screen and (max-width: 1024px) {
    .project__item {
      margin-bottom: 2rem;
    }
}